//This page is for the archive pages (the sidebar links!), and the Shop page (archive-products)



.shop-flex {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
}

.shop-title {
    display: block;
    text-align: center;
    margin-top: 100px;
    margin-bottom: 0;
    font-family: $font_secondary;
    font-weight: bold;
	text-transform: uppercase;
	font-size: 2.375rem;
	font-weight: bold;
}

.taxonomy-description {
    p {
        text-align: center;
    }
}

.shop-nav {
    text-decoration: none;
    text-align: center;
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    border-bottom: 1px dashed $dashed_border;
    
    li {
        text-transform: uppercase;
        padding: 0 25px 25px 25px;
        font-size: 1.1875rem;
        display: inline-block;

        a {
            color: #808080;
            text-decoration: none;

            p {
                font-family: $font_secondary;
            }
        }

        a:hover {
            color: #2f1339;
        }
    }
}


//The individual Shop products
.shop-product{
    display: inline-block;
    flex-basis: 20%;
    height: 300px;
    width: 20%;
    border: 1px solid #a1a1a1;
    margin: 15px;
    

    .text-wrapper {
        display: flex;
        justify-content: space-around;
        padding: 15px;
        font-size: 1rem;

        .title-text {
            overflow: hidden;
            white-space: nowrap;
        }

        //The '...'s between item name and price
        .title-text:after{
            content: '................................';
            display: inline;
            overflow: hidden;
        }

        p {
            display: inline-block;
            margin: 0;
        }

        
    }

    .image-wrapper {
        position: relative;
        height: 250px;
        overflow: hidden;

        img {
            position: absolute;
            left: -30%;
            border-bottom: 5px inset $solid_border;
            max-height: 250px;
            max-width: none;
            width: auto;
            // transform: translate(-50%,-52%);
        }
    }
    
}

.product {
    margin-top: 100px;


    .price-text {
        font-weight: bold;
        font-family: $font_secondary;
        font-size: 1.875rem;
    }

    .shop-item {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        padding: 0 0 100px 0;
        font-size: .875rem;

        .item-img {
            flex-basis: 50%;
        }

        .item-text {
            padding: 0 40px;
            flex-basis: 50%;

            p {
                font-size: 1rem;
                line-height: 28px;
            }

            h2 {
                font-size: 2.375rem;
                color: $color__link-hover;
                text-transform: uppercase;
                font-family: $font_secondary;
                font-weight: bold;
                margin: 0;
            }

            .price-text {
                font-size: 1.75rem;
                font-weight: normal;
            }

            button {
                margin: 0;
                width: 100px;

                .fa {
                    padding-right: 5px;
                }
            }
        }
    }
}

#primary-taxonomy {
    flex-basis: 73%;
    border-right: 1px solid $solid_border;
}

.archive {

    #primary {
        padding-top: 0;
    }

    .search-title {
        text-transform: uppercase;
        border-bottom: $solid-border;
        font-family: $font_secondary;
        font-weight: bold;
        font-size: 2.375rem;
        margin-bottom: 50px;
    }
}


//ADVENTURE 

.adventure {

    .adventure-img {
        height: 100vh;
        width: 100%;

        img {
            width: 100%;
            height: 100vh;
        }
    }

    .adventure-text {
        margin: 5% 15%;

        h2 {
            font-family: $font_secondary;
            text-transform: uppercase;
            font-size: 38px;
            margin-bottom: 0;
        }

        .author {
            margin: 0;
            font-size: 19px;
            text-transform: uppercase;
            font-family: $font_secondary;
        }

        p{
            font-size: 16px;
        }

        .read-more {
            color: black;
            padding: 10px 30px;
            margin-top: 20px;
        }
    }

}

.adventure-title {
    text-align: center;
    margin-top: 5%;
    padding: 30px;
    font-size: 38px;
    font-family: $font_secondary;
    text-transform: uppercase;
    border-bottom: $dashed-border;
}

.adventure-home {
    display: flex;
    width: 100%;
    margin: 0 auto;
    height: 1100px;
    flex-wrap: wrap;
    justify-content: space-around;

    .adventure-pic {
        display: inline-block;
        flex-basis: 48%;
        margin: 10px 0;
        justify-content: space-between;
        position: relative;
        overflow: hidden;

        img {
            display: inline;
            height: 100%;
            max-width: none;
            width: auto;
        }

        .colorMeDark {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,.3);
        }

        
    }
}

.adv-text {
    position: absolute;
    top: 20px;
    left: 20px;

    p {
        line-height: 3.5rem;
        color: white;
        font-size: 48px;    
        width: 80%;
        // margin: 5px 15%;
    }

    .read-more {
        background: rgba(0,0,0,0);
        border: 1px solid white;
        color: white;
        height: 50px;
    }

    .read-more:hover {
        background-color: #1f7a74;
        border: none;
        color: white;
    }

}


